import React, {useEffect} from 'react';

const SelectComp2 = (props) => {
    //props : options, value, setValue

    useEffect(() => {
        console.log(props.value)
    },[])
    const select_comp2 = {
        height: "40px",
    }
    
    const select_comp2_select = {
        width: "100%",
        height: "100%",
        border: 0,
        color: "#68B591",
        fontSize: "18px",
        fontWeight: "bold",
        fontFamily: 'GmarketSansMedium'
    }

    const setCategory = ({target}) => {
        props.setSelected(target.value)
    }

    return (
        <div style={select_comp2} >
            <select name="test" id="select_comp2-select" style={select_comp2_select} 
            onChange = {setCategory}>
                {
                    props.options.map((e, i) => {
                        return(
                            <option value={e} key={i}># {e}</option>
                        )
                    })
                }
            </select>
        </div>
    )
}

export default SelectComp2