import React, {useEffect, useState} from 'react';

const FeedCard = (props) => {
    const [data, setData] = useState(
        {
            userImage: "",
            userName: "",
            images: [],
            likes: 0,
            content: "",
            category: ""
        }
    )
    
    useEffect(() => {
        if(props.data.userImage.length === 0) props.data.userImage = "/images/default-profile.png"
        setData(props.data)
    }, [props])

    const feedcard = {
    }

    const feedcard_header = {
        height: "30px",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        padding: "0 20px"
    }

    const feedcard_user = {
        display: "flex",
        alignItems: "center",
    }

    const feedcard_userimage = {
        width: "30px",
        height: "30px",
        marginRight: "6px",
    }

    const feedcard_more = {
        display: "flex",
        alignItems: "center",
    }

    const feedcard_images = {
        padding: "12px 0",
        justifyContent: "center",
        textAlign: "center",
        
    }

    const feedcard_images_length = {
        fontSize: "12px"
    }

    const feedcard_bottom = {
        padding: "0 20px"
    }

    const feedcard_likes = {
        display: "flex",
        alignItems: "center",
    }

    const feedcard_likes_cnt = {
        marginLeft: "6px",
        color: "#68B591",
        fontSize: "16px"
    }

    const feedcard_detail = {
        fontFamily: 'Roboto'}

    const feedcard_detail_content = {
        padding: "12px 0",
        fontSize: "14px"
    }

    const feedcard_detail_category = {
        color: "#68B591",
        fontSize: "14px"
        
    }
    return (
        <div style={feedcard}>
            <div style={feedcard_header}>
                <div style={feedcard_user}>
                    <div style={feedcard_userimage}>
                        <img src={process.env.PUBLIC_URL + data.userImage} alt="" style={{width: "100%", height: "100%", borderRadius: "100%"}}/>
                    </div>
                    <div>{data.userName}</div>
                </div>
                <div style={feedcard_more}>
                    <img src={process.env.PUBLIC_URL + "/images/ellipsis.svg"} alt="" />
                </div>
            </div>
            <div style={feedcard_images}>
                <img src={process.env.PUBLIC_URL + data.images ? data.images : "/images/ex_image.png"} alt="" style={{width: "100%", height:"480px"}}/>
                {/*<div style={feedcard_images_length}>{data.images.length}</div>*/}
            </div>
            <div style={feedcard_bottom}>
                <div style={feedcard_likes}>
                    <img src={process.env.PUBLIC_URL + "/images/thumb_up.svg"} alt="" />
                    <div style={feedcard_likes_cnt}>{data.likes} </div>
                </div>
                <div style={feedcard_detail}>
                    <div style={feedcard_detail_content}>
                        {data.content}
                    </div>
                    <div style={feedcard_detail_category}>
                        #{data.category}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FeedCard