import React, {useState, useEffect} from 'react';
import './Challenge.css'
import Header from '../../components/Header'
import SlideComp from '../../components/SlideComp';
import BottomBar from "../../components/BottomBar";
import { useNavigate } from "react-router-dom";


const Challenge = () => {
    const navigate = useNavigate();
    console.log(navigate)
    const [challengeList, setChallengeList] = useState([{src:process.env.PUBLIC_URL + "/images/ex_image.png", id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}])
    const [myChallengeList, setMyChallengeList] = useState([{src:"/images/ex_image.png", id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}])
    const [recommend_challengeList, setrecommendChallengeList] = useState([{src:"", id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}])
    const linkToChallenge = (id) => {
        navigate("/challenge/detail",
            {state: {ChallengeId: id}}
        )
    }
    

    const getChallengeData = () => {
        fetch('/data/find_challenge_kind.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setChallengeList(data)
        })
        .catch((err) => {
            console.log(err);
        });
    }
    

    const getMyChallengeData = () => {
        fetch('/data/challenges.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setMyChallengeList(data)
        })
        .catch((err) => {
            console.log(err);
        });
    }

    useEffect(()=>{
        getChallengeData();
        getMyChallengeData()
    },[])

    return(
        <div className = 'challenge'>
            <div className='challenge-header'>
                <Header/>
            </div>
            <div className='challenge-title'> 
                <div className='challenge-img'>
                    <img src={process.env.PUBLIC_URL + "/images/challenges/challenge0.png"} alt="" />
                </div>
            </div>
            <div style={{padding: '0 20px'}}>
            {/* swiper ??*/}
                <div className="challenge-challengeList">
                    <SlideComp title={"  "} lists={myChallengeList} clickEvent={linkToChallenge} />
                </div>
                <div className="challenge-recommend-challengeList">
                    <SlideComp title={"  ?"} lists={challengeList} clickEvent={linkToChallenge} />
                </div>
            </div>
            <BottomBar />
        </div>
        
    )

}
export default Challenge;