import React, {useState, useEffect} from 'react';
import './Challenge_detail.css'
import Header from '../../components/Header'
import SelectComp2 from "../../components/SelectComp2";
import FeedCard from "../../components/FeedCard";
import BottomBar from "../../components/BottomBar";
import { useLocation } from 'react-router-dom';

const Challenge_detail = () => {
    const location = useLocation();
    //challengeID
    const [challengeID, setChallengeID] = useState(location.state.ChallengeId );
    const [challengeList, setchallengeList] = useState([" ", " ", " ", " "]);
    const [challengeData, setChallengeData] = useState([{src:"/images/ex_image.png", id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}])
    const [feedAllData, setFeedAllData] = useState([]);
    const [postData, setPostData] = useState([])

    const getChallengeData = () => {
        fetch('/data/find_challenge_kind.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setChallengeData(data[challengeID-1])
        })
        .catch((err) => {
            console.log(err);
        });
    }
    const getFeedAllData = () => {
        fetch('/data/find_all_feed.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                setFeedAllData(data)
            })
            .catch((err) => {
                console.log(err);
            });
    }

    useEffect(()=>{
        getChallengeData();
        getFeedAllData();
    },[])

    useEffect(() => {

        let challenge_name = challengeData.challenge_name + ' ';
        feedAllData.map((e) => {
            if (e.category === challenge_name) {
                setPostData(postData => [...postData, e])
            }
        })
    },[challengeData])
    return (

        <div className='challenge-detail'>
            <div className='challenge-detail-header'>
                <Header/>
            </div>
            <div className='challenge-detail-intro'>
                <div className="challenge-detail-title">{challengeData.challenge_name} </div>
                <img className= 'challenge-detail-img' src={process.env.PUBLIC_URL + challengeData.src} alt="" />
                <div className='challenge-detail-text'>{challengeData.challenge_description}</div>
            </div>
            <div className='challenge-detail-list'>
                {postData.length>0?
                    postData.map((e, i) => {
                        return (
                            <div className='challenge-detail-list-card' key={i}>
                                <FeedCard data={e} />
                            </div>
                        )
                    })
                :<p className='challenge-notExistPost'>   </p>
                }
            </div>
            <BottomBar />
        </div>
    )
}

export default Challenge_detail