import React, {useEffect, useState} from "react";
//swiper
import { Swiper, SwiperSlide } from "swiper/react";
import {Autoplay, Navigation, Pagination} from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import './Main.css'
import SlideComp from "../../components/SlideComp";
import Header from "../../components/Header";
import BottomBar from "../../components/BottomBar";
import { useNavigate } from "react-router-dom";

const Main = () => {
    const navigate = useNavigate();
    const [challengeList, setChallengeList] = useState([{src:'', id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}]);
    const [newsList, setNewsList] = useState([{src:'', link:"www.naver.com"}, {src:'', link:"www.google.com"}, {src:'', link:"www.naver.com"}, {src:'', link:"www.youtube.com"}, {src:'', link:"www.netflex.com"}]);
    const [econewsdata, setEcoNewsData] = useState([{src:'', id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}]);
    
    const linkToChallenge = (id) => {
        navigate("/challenge/detail",
            {state: {ChallengeId: id}}
        )
    }
    const linkToNews = (src) => {
        window.open(src)
        console.log(src);
    }

    const getChallengeData = () => {
        fetch('/data/find_challenge_kind.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setChallengeList(data)
            console.log(data);
        })
        .catch((err) => {
            console.log(err);
        });
    }

    const getEcoNewsData = () => {
        fetch('/data/Z979_ecoNews.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setEcoNewsData(data)
            console.log(data);
        })
        .catch((err) => {
            console.log(err);
        });
    }

    useEffect(()=>{
        getChallengeData();
        getEcoNewsData();
    },[])

    return(
        <div>
            <Header />
            <div className="main-banner">
                <Swiper
                    spaceBetween={30}
                    centeredSlides={true}
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    pagination={{
                        clickable: true,
                    }}
                    navigation={false}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <img src={process.env.PUBLIC_URL + "/images/swipers/swiper1.png"} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={process.env.PUBLIC_URL + "/images/swipers/swiper2.png"} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={process.env.PUBLIC_URL + "/images/swipers/swiper3.png"} alt="" />
                    </SwiperSlide>
                </Swiper>
            </div>
            <div style={{padding: '0 20px'}}>
                <div className="main-challengeList">
                    <SlideComp title={"#  "} lists={challengeList} clickEvent={linkToChallenge} />
                </div>
                <div className={"main-environment"}>
                    <p>#  </p>
                    <div>
                        <div>
                            <div className="main-eco-title">/</div>
                            <div className="main-eco-box">
                                <div className="main-eco-temp">
                                    <div className="main-eco-text">27</div>
                                </div>
                                <div className="main-eco-humi">
                                    <div className="main-eco-text">57%</div>
                                </div>
                            </div>
                        </div>
                        <div>
                            <div className="main-eco-title"></div>
                            <div className="main-eco-mise">
                                <div className="main-eco-vgood"></div>
                                <div className="main-eco-good"></div>
                                <div className="main-eco-vgood"></div>
                                <div className="main-eco-vgood"></div>
                                <div className="main-eco-vgood"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="main-NewsList">
                    <SlideComp title={"# "} lists={econewsdata} clickEvent={linkToNews} remote={true}/>
                </div>
            </div>
            <BottomBar />
        </div>
    )
}
export default Main;