import React, {useEffect, useState} from "react";
import { useNavigate } from "react-router-dom";
import './Profile.css'
import SlideComp from "../../components/SlideComp";
import Header from "../../components/Header";
import ButtonSmall from "../../components/ButtonSmall";
import LinearProgressWithLabel from '@mui/material/LinearProgress';
import Typography from '@mui/material/Typography';
import { Link } from "react-router-dom";
import BottomBar from "../../components/BottomBar";

const Profile = () => {
    const navigate = useNavigate();
    const [challengeList, setChallengeList] = useState([{src:'', id:1}, {src:'', id:2}, {src:'', id:3}, {src:'', id:4}, {src:'', id:5}]);
    const [badgeList, setBadgeList] = useState([]);
    const [user, setUser] = useState({
        name: "sowish23",
        id: 1,
        percent: 50,
        image: "/images/user1.jpeg",
        level: 3
    });
    
    const linkToChallenge = (id) => {
        console.log(id);
    }

    

    useEffect(() => {
        var badgelist = []
        for(var i=1; i<user.level+1; i++) {
            badgelist.push({
                src: `/images/badges/badge_lv${i}.svg`,
                id: i
            })
        }
        setBadgeList(badgelist)
    }, [user])

    const getChallengeData = () => {
        fetch('/data/challenges.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setChallengeList(data)
        })
        .catch((err) => {
            console.log(err);
        });
    }

    useEffect(()=>{
        getChallengeData();
    },[])

    return(
        <div className="profile">
            <div className="profile-header">
                <Header/>
            </div>
            <div className="profile-content">
                <div className="profile-myprofile">
                    <div className="profile-profilebox">
                        <div className="profile-profileimg">
                            <img src={process.env.PUBLIC_URL + user.image} alt="" />
                        </div>
                        <div className="profile-info">
                            <div className="profile-namebox">
                                <div className="profile-name">{user.name}</div>
                                <div className="profile-namebadge">
                                    <img src={process.env.PUBLIC_URL + `/images/badges/badge_lv${user.level}.svg`} alt="" />
                                </div>

                            </div>
                            <div className="profile-progress">
                                <LinearProgressWithLabel variant="determinate" value={user.percent}/>
                                <Typography variant="body2" color="text.secondary">{`${Math.round(
                                user.percent,
                                )}%`}</Typography>
                            </div>
                        </div>
                    </div>
                    <div className="profile-btnbox">
                        <Link to={"/profile/edit"}  style={{textDecoration: 'none'}}>
                            <div className="profile-btn" onClick={() => {}}>
                                <ButtonSmall name=" " border="1px solid #68B591" color="#68B591" fontWeight="bold"/>
                            </div>
                        </Link>
                        <div className="profile-btn"
                            onClick = {() => {
                                navigate("/feed",
                                    {state: {name: user.name}})
                            }}
                        >
                            <ButtonSmall name="  " background="#68B591" color="#FFFFFF" fontWeight="bold"/>
                        </div>
                        <Link to={"/stamp"}  style={{textDecoration: 'none'}}>
                            <div className="profile-btn">
                                <ButtonSmall name="  " background="#68B591" color="#FFFFFF" fontWeight="bold"/>
                            </div>
                        </Link>
                    </div>
                </div>
                <div className="profile-badge">
                    <SlideComp title={" "} lists={badgeList} />
                </div>
                <div className="profile-challenge">
                    <SlideComp title={" "} lists={challengeList} clickEvent={linkToChallenge} />
                </div>
            </div>
            <BottomBar />
        </div>
    )
}
export default Profile;