import React, {} from 'react';

const ButtonSmall = (props) => {
    const button = {
        backgroundColor: props.background,
        padding: 6,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: 10,
        border: props.border
    }

    const button_name = {
        fontSize: 12,
        color: props.color? props.color : '#030229',
        fontWeight: props.fontWeight ? props.fontWeight : "normal"
    }

    return (
        <div style={button}>
            <div style={button_name}>
                {props.name}
            </div>
        </div>
    )
}

export default ButtonSmall