import React, {} from 'react';

const Button = (props) => {
    const button = {
        backgroundColor: props.background,
        padding: 14,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: 10
    }

    const button_name = {
        fontSize: 16,
        color: props.color? props.color : '#030229',
        fontWeight: props.fontWeight? props.fontWeight : 'normal',
    }

    return (
        <div style={button}>
            <div style={button_name}>
                {props.name}
            </div>
        </div>
    )
}

export default Button