import * as React from "react";
import {useEffect, useState} from "react";
import styled from "styled-components";

const TreeComp = {
    textAlign: 'center',
    width: '90%',
    margin : '0 auto',
}
const LeafGroup = styled.div``;
const Leaf = styled.div`
  display : inline;
  & img {
    width: 10%;
  }
`;
const TrunkStyle = {
    width : '45%',
    margin : '0 auto',
}
const NumText = {
    fontSize : '12px',
    margin: 0,
}
const Tree = (props) => {
    const [lineBreak , setLineBreak] = useState([0,3,8,15])
    useEffect(() => {
    },[])
    return (
        <div style={TreeComp}>
            <LeafGroup>
                {[...Array(25)].map((n, index) => {
                    return (<Leaf>
                        {(index<props.number) ? (<img key={index} src={process.env.PUBLIC_URL + "/images/coloredLeaf.png"}/>) : <img key={index} src={process.env.PUBLIC_URL + "/images/notColoredLeaf.png"}/>}
                            {lineBreak.includes(index) ? <br/> : null}
                        </Leaf>
                    )
                })}
            </LeafGroup>
            <img src={process.env.PUBLIC_URL + "/images/treeTrunk.png"} style={TrunkStyle}/>
            <p style={NumText}>{props.number}/25 </p>
        </div>
    )
};

export default Tree;
