import React, {useState, useEffect} from 'react';
import './Login.css'
import Button from '../components/Button'
import InputTitle from '../components/InputTitle'
import {Link} from "react-router-dom";

const Login = () => {
    const [Userdata, setUserdata] = useState();
    const getUserData = () => {
        fetch('/data/profiles_user.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setUserdata(data)
            console.log(data)
        })
        .catch((err) => {
            console.log(err);
        });
    }

    useEffect(()=>{
        getUserData();
    },[])



    return (
        <div className='login'>
            <div className='login-top'>
                <div className='login-logo'>
                    <img src={process.env.PUBLIC_URL + "/images/logo-92.svg"} alt="" />
                </div>
                <div className='login-title'></div>
            </div>
            <div className='login-content'>
                <Button 
                    name={'  '}
                    background={'#FAE407'}/>
                <div className='login-linebox'>
                    <div className='login-line'></div>
                    <div className='login-line-title'></div>
                    <div className='login-line'></div>
                </div>
                <div className='login-input-title'>
                    <InputTitle label={'Email'}/>
                </div>
                <div className='login-input-title'>
                    <InputTitle label={'Password'}/>
                </div>
                <Link to={"/"}  style={{textDecoration: 'none'}}>
                    <div className='login-login-btn'>
                        <Button
                            name={''}
                            background={'#79BD9A'}
                            color={'#FFFFFF'}
                        />
                    </div>
                </Link>
            </div>
            <div className='login-bottom'>
                <div className='login-bottom-text'>  </div>
                <Link to={"/register"}  style={{textDecoration: 'none'}}>
                    <div className='login-bottom-text-link'> </div>
                </Link>
            </div>
        </div>
    )
}

export default Login