import React from "react";

const SlideComp = (props) => {
    //props : title ? , lists [], clickEvent
    const PhotoTitle = {
        margin : '0 0 10px 0',
        fontSize : 16,
        color : '#68B591',
        fontWeight: 'bold',
        textAlign: 'left'
    }
    const lists = {
        textAlign: 'left',
        display : 'flex',
        flexDirection : 'row',
        gap: 10,
        overflowX: 'auto',
        overflowY:'hidden',
        height: 100,
    }
    const photoElement = {
        // backgroundColor : '#F7F7F8',
        height: '100%',
        width : '100px',
        display: 'inline-block',
        flex: '0 0 auto',
    }
    return(
        <div>
            {props.title ? <p style={PhotoTitle}>{props.title}</p> : null }
            <div style={lists}>
                {props.lists.map((p) => (
                    props.clickEvent?
                    <img src={props.remote ? p.src : process.env.PUBLIC_URL + p.src} style={photoElement} alt="" onClick={() => {props.clickEvent(p.id?p.id:p.link)}} />
                    :<img src={props.remote ? p.src : process.env.PUBLIC_URL + p.src} style={photoElement} alt="" />
                ))}
            </div>
        </div>
    )
}
export default SlideComp;