import React, {useState, useEffect} from 'react';
import { useLocation } from 'react-router-dom';
import './Feed.css'
import Header from '../../components/Header'
import SelectComp2 from "../../components/SelectComp2";
import FeedCard from "../../components/FeedCard";
import BottomBar from "../../components/BottomBar";

const Feed = () => {
    const location = useLocation();
    
    console.log(location.state)

    const [feedAllData, setFeedAllData] = useState([]);
    const [showData, setShowData] = useState([]);
    const [myFeedData, setMyFeedData] = useState([]);
    const [options, setOptions] = useState(["", " ", " ", " ", " "]);
    const [selected, setSelected] = useState("");
    
    
    const [data, setData] = useState([
        {
            userImage: "",
            userName: "",
            images: ["/images/ex_image.png", "/images/ex_image.png", "/images/ex_image.png"],
            likes: 8,
            content: "  .    ",
            category: " "
        },
        {
            userImage: "/images/logo-92.png",
            userName: "",
            images: ["/images/ex_image.png", "/images/ex_image.png"],
            likes: 37,
            content: "  .    ",
            category: " "
        }
    ])

    const getMyFeedData = () => {
        fetch('/data/my_feed.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setMyFeedData(data)
            console.log(data)
        })
        .catch((err) => {
            console.log(err);
        });
    }
    useEffect(()=>{
        getMyFeedData();
    },[])

    useEffect(() => {
        let data = []

        if(selected === ""){
            feedAllData.map((e,i) => {
                if(!e.is_challenge){
                    data.push(e)
                }
            })
        }
        else if(selected === " "){
            feedAllData.map((e,i) => {
                if(e.is_challenge){
                    data.push(e)
                }
            })
        }
        else{
            feedAllData.map((e,i) => {
                if(e.category === selected){
                    data.push(e)
                }
            })
        }
        setShowData(data)

    }, [selected])

    
    const getFeedallData = () => {
        let firstData = []

        fetch('/data/find_all_feed.json'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }
        )
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            setFeedAllData(data)
            
            data.map((e,i) => {
                if(!e.is_challenge){
                    firstData.push(e)
                }
            })
            setShowData(firstData)
            // console.log(feedAllData)
        })
        .catch((err) => {
            console.log(err);
        });
    }
    useEffect(()=>{
        getFeedallData();
        
    },[])

    
    

    return (
        <div className='feed'>
            <div className='feed-header'>
                <Header/>
            </div>
            {
                location.state !== null ? null :
                <div className='feed-selectbox'>
                    <SelectComp2 options={options} setSelected = {setSelected}/>
                </div>

            }
            
            
            <div className='feed-list'>
                {
                    location.state ? 
                    feedAllData.map((e,i) => {
                        if(e.userName == location.state.name){
                            return (
                                <div className='feed-list-card' key={i}>
                                    <FeedCard data={e} />
                                </div>
                            )
                        }
                    })
                        :
                    
                    showData.map((e, i) => {
                        return (
                            <div className='feed-list-card' key={i}>
                                <FeedCard data={e} />
                            </div>
                        )
                    })
                }
            </div>
            <BottomBar />
        </div>
    )
}

export default Feed