import React, {} from 'react';

const InputTitle = (props) => {
    const input_title = {
        textAlign: 'left'
    }

    const input_label = {
        fontSize: 16
    }

    const input_content = {
        marginTop: 12
    }
    
    const input_input = {
        border: 0,
        backgroundColor: "#F7F7F8",
        padding: 16,
        width: "100%",
        boxSizing: "border-box",
        borderRadius: 10
    }
    
    return (
        <div style={input_title}>
            <div style={input_label}>
                {props.label}
            </div>
            <div style={input_content}>
                <input 
                    type="text"
                    style={input_input}
                    defaultValue={props.value}
                />
            </div>
        </div>
    )
}

export default InputTitle