import React, {useEffect, useState} from 'react';
import {FormControl, FormControlLabel, FormLabel, Radio, RadioGroup} from "@mui/material";

const RadioBtn = (props) => {
    //props : title, value, setValue, option1, option2
    const [selected, setSelected] = useState(props.value);
    const radioGroup = {
        display: 'flex',
        flexDirection:'row'
    }
    const Title = {
        fontSize:16,
        marginBottom: 10
    }
    useEffect(() => {
        props.setValue(selected)
    }, [selected])
    return(
        <FormControl>
            <p style={Title}>{props.title}</p>
            <RadioGroup
                style={radioGroup}
                aria-labelledby="demo-controlled-radio-buttons-group"
                name="controlled-radio-buttons-group"
                value={selected}
            >
                <FormControlLabel value={props.option1} control={<Radio/>} onClick={() => {setSelected(props.option1)}} label={props.option1} />
                <FormControlLabel value={props.option2} control={<Radio/>} onClick={() => {setSelected(props.option2)}} label={props.option2} />
            </RadioGroup>
        </FormControl>
    )
}
export default RadioBtn;